.contacts-main {
    padding: 50px 0;

    .contacts-main__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 40px;
    } 

    .contacts-main__text {
        font-family: Inter;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        margin: 0 0 30px;
    }
}