.faq-main {
    padding: 50px 0;

    .faq-main__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 40px;
    }

    .faq-main__text {
        font-family: Inter;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        margin: 0 0 30px;
    }

    .accordion {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .accordion__item {
        width: 100%;
        border-bottom: 1px solid grey;
        padding-bottom: 20px;
    }

    .accordion__button {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        align-items: center;
        width: 100%;
    }

    .accordion__button span {
        
        font-family: Inter;
        font-weight: 500;
        font-style: Regular;
        font-size: 25px;
        line-height: 130%;
    }

    .accordion__inner {
        margin-top: 20px;
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        width: 90%;
    }

    .accordion__button svg {
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        transition: .3s;
    }

    .accordion__item.is-active .accordion__button svg {
        transform: rotate(45deg);
    }

    .faq-main__bottom {
        text-align: center; 
        margin-top: 40px; 
    }

    .faq-main__bottom p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
    }

    .faq-main__link {
        max-width: 320px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        line-height: 130%;
        color: #fff;
        margin: 0 auto;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
    }

    .faq-main__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .faq-main__link:hover svg {
        transform: rotate(45deg);
    }
}