.make {
    overflow: hidden;

    &__wrapper {
        padding: 75px 0;
    }

    &__top {
        display: grid;
        grid-template-columns: 1fr 0.2fr;
        gap: 20px;
        margin-bottom: 25px;
    }

    &__top-info p {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 200px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    &__top-image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 185/255;
    }

    &__middle {
        display: flex;
        margin-bottom: 25px;
        gap: 90px;
    }

    &__middle-image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 163/140;
        align-self: end;
        width: 17%;
    }

    &__middle-info p {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 200px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    &__middle-info p:first-child {
        margin-left: 100px;
    }

    &__bottom span {
        display: block;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 40px;
    }

    &__bottom-info {
        display: flex;
        gap: 20px;

    }

    &__bottom-info p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }
}