.matter {
    overflow: hidden;

    &__wrapper {
        padding: 75px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 60px 20px;
    }

    &__name {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__desc {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }
}