.artist-head {
    overflow: hidden;
    &__wrapper {
        padding: 75px 0;
    }

    &__top {
        display: flex;
        gap: 110px;
        margin-bottom: 40px;
    }

    &__top-image {
        width: 27%;
        max-width: 100%;
        height: auto;
        aspect-ratio: 255/330;
    }

    &__top-info {
        display: flex;
        flex-direction: column;
        align-self: end;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__text {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 650px;
        margin: 0;
    }

    &__middle {
        display: flex;
        gap: 20px;
        margin-bottom: 40px;
        margin-left: 505px;
    }

    &__middle-image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 530/250;
        width: 82.5%;
    }

    &__bottom {
        display: flex;
        gap: 20px;
    }

    &__bottom-image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 165/250;
        width: 18%;
    }

    &__bottom-info {
        max-width: 440px;
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    &__bottom-text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        margin: 0 0 20px;
        color: #181818;
    }

    &__bottom-subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        margin: 0 0 20px;
        color: #181818;
    }


    &__link {
        max-width: 165px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
        margin-top: auto;
    }

    &__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link:hover svg {
        transform: rotate(45deg);
    }
}