.packages {
    padding: 50px 0;

    .packages__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 30px;
    }

    .packages__list {
        display: grid; 
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); 
        gap: 24px;
    }

    .packages__card {
        background: #fafafa; 
        border-radius: 12px; 
        overflow: hidden; 
        border: 1px solid #eee;
        display: flex;
    }

    .packages__card a {
        display: flex;
        flex-direction: column;
    }

    .packages__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 335/180;
    }

    .packages__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .packages__content {
        padding: 15px 15px 20px;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    .packages__content h3 {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        line-height: 110%;
        margin: 0 0 20px;
    }

    .packages__content p {
        font-family: Inter;
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: #666;
        margin: 0 0 10px;
    }

    .packages__content span {
        font-family: Inter;
        font-weight: 600;
        font-size: 14px;
        line-height: 130%;
        color: #000;
        display: block;
        margin-top: auto;
    }

    .packages__bottom {
        margin-top: 40px; 
        padding: 30px; 
        background: #f8f9fa; 
        border-radius: 16px; 
        border: 1px solid #e9ecef;
    }

    .packages__bottom-list {
        display: grid; 
        grid-template-columns: 1fr 1.2fr; 
        gap: 40px; 
        align-items: center;
    }

    .packages__bottom-text h2 {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 24px;
        line-height: 110%;
        margin: 0 0 20px;
    }

    .packages__bottom-text p {
        font-family: Inter;
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        color: #000;
        margin: 0 0 25px;
    }

    .packages__bottom-price {
        margin: 25px 0; 
        padding: 15px; 
        background: #fff; 
        border-radius: 12px; 
        border: 2px solid #1b6cff; 
        display: inline-block;
    }

    .packages__bottom-price p {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 24px;
        line-height: 110%;
        margin: 0;
    }

    .packages__bottom-price p span {
        color: #1b6cff;
    }

    .packages__bottom-buttons {
        display: flex; 
        gap: 15px; 
        margin: 25px 0;
    }

    .packages__bottom-link {
        max-width: 190px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
    }
    
    .packages__bottom-link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .packages__bottom-link:hover svg {
        transform: rotate(45deg);
    }

    .packages__bottom-link-white {
        max-width: 190px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    .packages__bottom-link-white svg {
        width: 10px;
        height: 10px;
        -ms-flex-negative: 0;
        flex-shrink: 0;
        transition: .3s;
    }

    .packages__bottom-link-white:hover svg {
        transform: rotate(45deg);
    }

    .packages__bottom-note {
        margin-top: 20px; 
        padding-top: 20px; 
        border-top: 1px solid #dee2e6;
    }

    .packages__bottom-note p {
        margin: 0; 
        font-size: 15px; 
        color: #666; 
        font-style: italic;
    }
}