.studio {
    padding: 150px 0 0;

    &__top {
        display: flex;
        gap: 20px;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    &__image-top {
        max-width: 100%;
        height: auto;
        aspect-ratio: 530/115;
        width: 50%;
    }

    &__image-top img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 160px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: right;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    &__middle {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 32px;
    } 

    &__texts {
        display: flex;
        flex-direction: column;
    }

    &__text {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 160px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    &__subtext {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 160px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    &__image-middle {
        max-width: 100%;
        height: auto;
        aspect-ratio: 255/255;
    }

    &__image-middle img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
        margin-bottom: 80px;
    }

    &__bottom-text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
        max-width: 650px;
        width: 100%;
    }

    &__bottom-subtext {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 160px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: right;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }
}