.footer {
  background: #615F5E;
  overflow: hidden;
  
  &__wrapper {
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @media screen and (max-width: 1120px) {
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      gap: 40px;
    }

    @media screen and (max-width: 980px) {
      flex-wrap: wrap;
      gap: 40px;
    }

    @media screen and (max-width: 850px) {
      flex-direction: column;
      align-items: center;
    }

    @include vp-767 {
      flex-direction: column;
      
    }

    @media screen and (max-width: 520px) {
      justify-content: center;
    }
  }

  &__left {
    display: flex;
    gap: 15px;
    align-items: center;
    
    @media screen and (max-width: 850px) {
      flex-direction: column;
    }

    @include vp-767 {
      flex-direction: column;
    }
  }

  &__icons {
    display: flex;
    gap: 15px;

    a {
      svg {
        transition: .3s;
        width: 33px;
        height: 33px;
        flex-shrink: 0;
      }
    }
  }

  &__icons a:hover {
    svg {
      opacity: 0.8;
    }
  }

  &__data {
    display: flex;
    // flex-direction: column;
    gap: 20px;

    a {
      display: block;
      font-family: Geologica;
      font-weight: 400;
      font-style: Regular;
      font-size: 14px;
      leading-trim: CAP_HEIGHT;
      line-height: 100%;
      letter-spacing: 0%;
      vertical-align: middle;
      color: rgba(26, 26, 24, 1);
      transition: .3s;
    }

    a:hover {
      opacity: 0.8;
    }

    @include vp-767 {
      flex-direction: column;
      text-align: center;
    }
  }



  &__right a {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: start;
    transition: .3s;
  }

  &__right a:hover {
    opacity: 0.8;
  }

  &__center {
    a {
      font-family: Geologica;
      font-weight: 400;
      font-style: Regular;
      font-size: 14px;
      leading-trim: CAP_HEIGHT;
      line-height: 150%;
      letter-spacing: 0%;
      vertical-align: middle;
      color: rgba(26, 26, 24, 1);
      transition: .3s;
    }

    a:hover {
      opacity: 0.8;
    }

    @media screen and (max-width: 672px) {
      margin: 0 auto;
    }
  }

  &__doc {
    font-family: Geologica;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 150%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: rgba(26, 26, 24, 1);
    margin: 0;
  }

  &__top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
  }

  &__name {
    font-family: RobotoCondensed;
    font-weight: 700;
    font-style: Bold;
    font-size: 20px;
    leading-trim: NONE;
    line-height: 110.00000000000001%;
    letter-spacing: 0%;
    vertical-align: middle;
    text-transform: uppercase;
    color: #84807F;
    margin: 0 0 20px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__link {
    font-family: RobotoMono;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: #FFFFFF;
    transition: .3s;
  }

  &__link:hover {
    opacity: 0.8;
  }

  &__phones {
    display: flex;
    align-items: center;
    gap: 35px;
  }

  &__phone {
    font-family: RobotoMono;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    text-align: center;
    vertical-align: middle;
    text-decoration: underline;
    color: #FFFFFF;
    transition: .3s;
  }

  &__phone:hover {
    opacity: 0.8;
  }


  &__bottom {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    align-items: center;
  }

  &__low {
    font-family: RobotoMono;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    vertical-align: middle;
    color: #BDB9B8;
    margin: 0;
  }


}