.buziness {
    &__wrapper {
        padding: 50px 0;
    }
    
    &__top {
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
    }

    &__top span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 120px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #CAC6C5;
        margin: 0;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px 20px;
    }

    &__item {
        display: flex;
        flex-direction: column;

    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 350/250;
        margin-bottom: 20px;
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__info {
        display: flex;
        flex-direction: column;
    }

    &__name {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 10px;
    }

    &__desc {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 30px;
    }

    &__more {
        max-width: 165px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        border: 1px solid #181818;
        padding: 10px;
        border-radius: 20px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    &__more svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__more:hover svg {
        transform: rotate(45deg);
    }
}