.breadcrumbs {
  width: 100%;
  padding: 20px 0;
  position: relative;
  z-index: 1;

  // .container {
  //   padding: 0 0 0 20px;

  //   @media screen and (max-width: 768px) {
  //     padding: 0 0 0 20px;
  //   }

  //   @media screen and (max-width: 700px) {
  //     padding: 0 45px;
  //   }
  // }

  /*@include vp-1023 {
    padding: 30px 0 40px;
  }

  @include vp-767 {
    padding: 20px 0 40px;
  }*/

  &--absolute {
    z-index: 1;
    position: absolute;

    top: 0;
    left: 0;
  }

  .breadcrumbs__list {
    display: flex;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 7px 0;
  }

  &__item {
    display: flex;
    align-items: center;

    svg {
      flex-shrink: 0;
      width: 10px;
      height: 11px;
      margin: 0 5px;
      fill: black;

    }

    &:last-child {

      svg {
        display: none;
      }
    }
  }

  &__item:last-child>&__link {
    color: #181818;
    pointer-events: none;
  }

  &__link {
    font-family: RobotoMono;
    font-weight: 400;
    font-style: Regular;
    font-size: 14px;
    leading-trim: NONE;
    line-height: 130%;
    letter-spacing: 0%;
    text-align: center;
    vertical-align: middle;
    color: #BDB9B8;

    transition: color $trans-default;

    @include hover-focus {
      color: #181818;
    }

    @include active {
      color: #181818;
    }

    &.is-active {
      color: #181818;

      pointer-events: none;
    }
  }

  // @media screen and (max-width: 768px) {
  //   padding: 20px 0;
  // }
  @media screen and (max-width: 768px) {
    &__link {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 390px) {
    // padding: 0;

    // .container {
    //   padding: 10px 35px 20px;
    // }

    &__link {
      font-size: 14px;
    }
  }
}