.med-head {
    overflow: hidden;
    &__wrapper {
        padding: 75px 0;
        margin-left: 250px;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 40px;
    }

    &__top-info {
        align-self: end;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__text {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
        max-width: 610px;
    }

    &__top-image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 255/330;
        width: 30%;
    }

    &__top-image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__middle {
        display: flex;
        gap: 20px;
        margin-bottom: 40px;
    }

    &__middle-image1 {
        max-width: 100%;
        height: auto;
        aspect-ratio: 165/250;
        width: 18%;
    }

    &__middle-image1 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__middle-image2 {
        max-width: 100%;
        height: auto;
        aspect-ratio: 350/250;
        width: 36.5%;
    }

    &__middle-image2 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__bottom-info {
        display: flex;
        flex-direction: column;
    }

    &__bottom-text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 20px;
    }

    &__bottom-subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0px;
        max-width: 450px;
        width: 100%;
    }

    &__link {
        max-width: 165px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
        margin-top: auto;
    }

    &__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link:hover svg {
        transform: rotate(45deg);
    }

    &__bottom-images {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        width: 70%;
    }

    &__middle-image3 {
        max-width: 100%;
        height: auto;
        aspect-ratio: 164/250;
    }

    &__middle-image3 img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}