.contacts {
    padding: 150px 0 0;

    &__wrapper {
        padding: 0 0 50px;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 70px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 70px;
    }

    &__item {
        display: flex;
        gap: 120px;
        align-items: flex-start;
    }


    &__name {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #BDB9B8;
        margin: 0;
    }

    &__box a {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        // text-transform: uppercase;
        color: #181818;
        transition: .3s;
    }

    &__box a:hover {
        opacity: 0.8;
    }

    &__box span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }
}