.pricing {
    overflow: hidden;

    &__wrapper {
        padding: 75px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        color: 0 0 40px;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px 20px;
    }

    &__item {
        display: flex;
        flex-direction: column;
    }

    &__item span {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #E2DDD8;
        display: block;
        position: relative;
        z-index: 1;
    }

    &__info {
        position: relative;
        z-index: 2;
        border-top: 2px solid #BDB9B8;
        background: #fbf4eb;
        margin-top: -18px;
        padding-top: 20px;
    }

    &__name {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__sum {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 20px;
    }

    &__info ul {
        list-style: disc;
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
        padding-left: 20px;
    }

    &__info ul li {
        list-style: disc;
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }


    &__link {
        max-width: 165px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    &__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link:hover svg {
        transform: rotate(45deg);
    }
}