.blog {
    overflow: hidden;
    padding: 150px 0 0;

    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 10px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }

    .blog-content__list {
        border-top: 1px solid #BDB9B8;
        border-bottom: 1px solid #BDB9B8;
        display: flex;
        flex-direction: column;
        gap: 40px;
        
    }

    .blog-content__item {
        padding: 40px 0 40px 180px;
        position: relative;
    }

    .blog-content__item::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        background: #BDB9B8;
        left: 0;
        bottom: -20px;
    }

    .blog-content__item:last-child::after {
        display: none;
    }

    .blog-content__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    .blog-content__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0;
    }

    .blog-content__date {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        text-align: right;
        vertical-align: middle;
        margin: 0;
        color: #BDB9B8;
    }

    .blog-content__info {
        display: flex;
        gap: 35px;
    }

    .blog-content__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 150/100;
        width: 20%;
    }

    .blog-content__desc {
        display: flex;
        flex-direction: column;
    }

    .blog-content__text {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 20px;
        max-width: 670px;
    }

    .blog-content__link {
        margin-top: auto;
        max-width: 165px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    .blog-content__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .blog-content__link:hover svg {
        transform: rotate(45deg);
    }
}