.pay {
    padding: 150px 0 0;

    &__wrapper {
        padding: 30px 0 75px;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 15px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 60px;
        max-width: 630px;
    }

    
    &__content {
        position: relative;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
    
    &__item {
        padding: 20px 20px 20px 10px;
        position: relative;
    }
    
      
    &__item:nth-child(1)::after,
    &__item:nth-child(2)::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: -10px; //  gap
        height: 1px;
        background: #BDB9B8;
    }
    
      /*      */
    &__item:nth-child(2)::before,
    &__item:nth-child(4)::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: -10px; //  gap
        width: 1px;
        background: #BDB9B8;
    }

    &__item span {
        display: block;
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__desc {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }
    
    
    &__icons {
        display: flex;
        gap: 40px;
    }

    &__icons svg {
        width: 40px;
        height: 40px;
        flex-shrink: 0;
    }
      

    
      
}