.event-hero {
    padding: 150px 0 0;

    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 40px;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 1420/946;
        margin-bottom: 40px;
    }

    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__buttons {
        width: 100%;
        justify-content: center;
        display: flex;
        gap: 15px;
        margin-bottom: 20px;
    }

    &__link {
        max-width: 220px;
        width: 100%;
        border-radius: 20px;
        padding: 13px;
        background: #181818;
        color: #fff;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0;
        text-align: center;
        vertical-align: middle;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
    }

    &__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link:hover svg {
        transform: rotate(45deg);
    }

    &__link-white {
        border: 1px solid #181818;
        color: #181818;
        max-width: 220px;
        width: 100%;
        border-radius: 20px;
        padding: 13px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0;
        text-align: center;
        vertical-align: middle;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
    }

    &__box span {
        font-family: Inter;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
    }

    
}