.top-lawyer {
    padding: 150px 0 0;

    &__wrapper {
        padding: 50px 0 20px;
        display: flex;
        gap: 20px;
        justify-content: space-between;
    }

    &__info {
        align-self: end;
    }

    &__title {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 920px;
        margin: 0 0 20px;
    }

    &__text {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__links {
        display: flex;
        gap: 20px;
    }

    &__link {
        max-width: 165px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
    }

    &__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link:hover svg {
        transform: rotate(45deg);
    }

    &__link-white {
        max-width: 165px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    &__link-white svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__link-white:hover svg {
        transform: rotate(45deg);
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 310/400;
    }
}