.hero {
    padding: 150px 0 0;

    .hero-inner {
        display: flex;
        gap: 20px;
        padding: 50px 0;
    }
      
    .hero-copy {
        width: 50%;
    }

    .hero-copy__small span {
        font-size: clamp(24px, 1.2vw, 18px); 
        font-weight: 600;  
        letter-spacing: 1px; 
        text-transform: uppercase;
        display: block;
        margin: 0 0 50px;
    }

    .hero-copy__title {
        font-family: RobotoCondensed;
        font-size: clamp(44px, 6vw, 68px); 
        line-height: 1.05;
        margin: 0 0 50px;
        text-transform: uppercase;
    }

    .hero-copy__rew {
        padding: 16px; 
        max-width: 320px; 
        margin-bottom: 40px; 
    }

    .hero-copy__icon {
        display: flex; 
        // align-items: center; 
        gap: 2px; 
        margin-bottom: 8px;
    }

    .hero-copy__stars {
        color: #fbbf24; 
        font-size: 20px; 
        letter-spacing: 5px;
        line-height: 1;
    }

    .hero-copy__num {
        font-size: 18px; 
        color: #000;
        font-weight: 700;
        line-height: 135%;
    }

    .hero-copy__rew-text {
        margin: 0 0 8px; 
        font-weight: 600; 
        font-size: 18px; 
        color: #000;
    }

    .hero-copy__rew-link {
        color: #93c5fd; 
        text-decoration: underline; 
        font-size: 18px; 
        display: inline-block;
    }

    .hero-copy__list {
        margin-bottom: 24px;
        display: flex;
        flex-direction: column;
        gap: 12px; 
    }

    .hero-copy__item {
        display: flex; 
        align-items: flex-start;
        gap: 10px;
    }

    .hero-copy__svg {
        color: #4ade80; 
        font-size: 18px; 
        flex-shrink: 0;
    }

    .hero-copy__item-text {
        font-size: clamp(15px, 1.6vw, 17px); 
        color: #000;
    }

    .hero-copy__buttons-top {
        display: flex;
        gap: 10px;
        margin-bottom: 40px;
    }

    .hero-copy__buttons-top-link {
        max-width: 190px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
    }

    .hero-copy__buttons-top-link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .hero-copy__buttons-top-link-white {
        max-width: 190px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    .hero-copy__buttons-top-link-white svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .hero-copy__buttons-top-link:hover svg {
        transform: rotate(45deg);
    }

    .hero-copy__buttons-top-link-white:hover svg {
        transform: rotate(45deg);
    }

    .hero-copy__buttons-blue {
        color: #93c5fd; 
        text-decoration: underline; 
        font-size: 16px;
    }

    .hero-image {
        width: 50%;
        max-width: 100%; 
        height: auto; 
        aspect-ratio: 800/800;
        border-radius: 16px; 
        box-shadow: 0 12px 32px rgba(0,0,0,0.2);
        overflow: hidden;
    }
      
    p.lead {
        font-size: clamp(14px, 1.6vw, 18px);
        margin: 0 0 18px;
        color: rgba(255,255,255,0.95);
    }
      
    .cta {
        display: inline-block;
        background: #fff;
        color: var(--dark);
        padding: 12px 18px;
        border-radius: 8px;
        font-weight: 600;
    }
      
    .cta.secondary {
        background: transparent;
        color: #fff;
        border: 1px solid rgba(255,255,255,0.3);
    }
      
}