.privacy {
    padding: 150px 0 0;

    &__wrapper {
        padding: 30px 0 75px;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-left: 200px;
    }

    &__content p {
        font-family: Inter;
        font-weight: 400;
        font-style: Bold;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #BDB9B8;
        margin: 0;
    }
}