.foto {
    &__wrapper {
        padding: 0 0 75px;
    }

    &__slider {
        width: 100%;
        height: 100%;
    }

    .swiper-wrapper {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 20px;

        @include vp-1023 {
            display: flex;
            gap: 0;
        }
    }

    .swiper-slide {

        @include vp-1023 {
           width: 165px;
        }
        
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 5px;
    }

    &__num {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #BDB9B8;
        margin: 0;
    }

    &__name {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        text-align: right;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 165/200;
        position: relative;
        transition: .3s;
    }

    &__link {
        width: 100%;
        position: relative;
        display: block;
    }

    &__link img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__more {
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #FFFFFF;
        padding: 0px 0px 4px;
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        opacity: 0;
        z-index: 1;
        transition: opacity .3s ease;

        @include vp-1023 {
            opacity: 1;
        }
    }

    &__more span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #FFFFFF;
    }

    &__more svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
    }

    
    .foto__link::after {
        content: '';
        position: absolute;
        inset: 0;
        background: #18181880;
        opacity: 0;
        transition: opacity .3s ease;
        pointer-events: none;

        @include vp-1023 {
            opacity: 1;
        }
    }

    .foto__link:hover::after {
        opacity: 1;
    }
    
    .foto__link:hover .foto__more {
        opacity: 1;
    }
    
    
}