.steps {
    overflow: hidden;

    &__wrapper {
        padding: 75px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 30px;
        position: relative;
    }

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        gap: 12px;
        padding-top: 12px;

        // //  
        // &::before {
        //     content: '';
        //     position: absolute;
        //     top: 0;
        //     left: 0;
        //     right: 0;
        //     height: 1px;
        //     background: #BDB9B8;
        // }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
        
            height: 25px;
            transform: translateY(-12px);
        
            background-image:
                /*    */
                linear-gradient(#BDB9B8, #BDB9B8),
        
                /*    */
                linear-gradient(#BDB9B8, #BDB9B8),
        
                /*   */
                linear-gradient(
                    to bottom,
                    transparent 12px,
                    #BDB9B8 12px,
                    #BDB9B8 13px,
                    transparent 13px
                ),
        
                /*   */
                repeating-linear-gradient(
                    to right,
                    transparent 0,
                    transparent 179px,
                    #BDB9B8 179px,
                    #BDB9B8 180px
                );
        
            background-size:
                1px 25px,   /*  */
                1px 25px,   /*  */
                100% 25px,  /*  */
                180px 25px; /*  */
        
            background-position:
                left top,
                right top,
                left top,
                left top;
        
            background-repeat:
                no-repeat,
                no-repeat,
                no-repeat,
                repeat;
        }
        
    }

    &__num {
        font-family: RobotoMono;
        font-weight: 700;
        font-style: Bold;
        font-size: 140px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        margin: 0;
        color: #181818;
        flex-shrink: 0;
    }

    &__subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: -20px 0 40px;
    }

    &__text {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
        align-self: end;
    }

    /*   () */
    &__item--1 {
        padding-left: 200px;
    }

    &__item--2 {
        padding-left: 370px;
    }

    &__item--3 {
        padding-left: 550px;
    }

    &__item--4 {
        padding-left: 730px;
        
    }

    &__item:last-child {
        border-bottom: 1px solid #BDB9B8;
        padding-bottom: 24px;
    }

    &__item:last-child::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    
        height: 25px;
        transform: translateY(12px); //    
    
        background-image:
            /*    */
            linear-gradient(#BDB9B8, #BDB9B8),
    
            /*    */
            linear-gradient(#BDB9B8, #BDB9B8),
    
            /*   */
            linear-gradient(
                to bottom,
                transparent 12px,
                #BDB9B8 12px,
                #BDB9B8 13px,
                transparent 13px
            ),
    
            /*   */
            repeating-linear-gradient(
                to right,
                transparent 0,
                transparent 179px,
                #BDB9B8 179px,
                #BDB9B8 180px
            );
    
        background-size:
            1px 25px,
            1px 25px,
            100% 25px,
            180px 25px;
    
        background-position:
            left top,
            right top,
            left top,
            left top;
    
        background-repeat:
            no-repeat,
            no-repeat,
            no-repeat,
            repeat;
    }
    
}