.how-to-book {
    padding: 50px 0;

    .how-to-book__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 30px;
    }

    .how-to-book__list {
        display: grid; 
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); 
        gap: 20px; 
    }

    .step-card h3 {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        line-height: 110%;
        text-transform: uppercase;
        margin: 0 0 20px;
    }

    .step-card .small {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        line-height: 160%;
        margin: 0;
    }

    .step-card__list {
        display: flex;
        flex-direction: column;
        margin-top: 10px;
    }

    .step-card__list span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        line-height: 160%;
        margin: 0;
    }
}