.event-faq {
    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 50px;
    }

    .accordion {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .accordion__item {
        width: 100%;
        border-bottom: 1px solid grey;
        padding-bottom: 20px;
    }

    .accordion__button {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        align-items: center;
        width: 100%;
    }

    .accordion__button span {
        
        font-family: Inter;
        font-weight: 500;
        font-style: Regular;
        font-size: 25px;
        line-height: 130%;
    }

    .accordion__inner {
        margin-top: 20px;
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        width: 90%;
    }

    .accordion__button svg {
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        transition: .3s;
    }

    .accordion__item.is-active .accordion__button svg {
        transform: rotate(45deg);
    }
}