.single-blog {
    overflow: hidden;
    padding: 150px 0 0;

    &__wrapper {
        padding: 50px 0 75px;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 10px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 620px;
        margin: 0 0 50px;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 1080/720;
        margin-bottom: 75px;
    }

    &__subtitle1 {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 40px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 50px;
    }

    &__box {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 75px;
    }

    &__box p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 690px;
        margin: 0;
    }

    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;
        margin-bottom: 80px;
    }


    &__item {
        display: flex;
        justify-content: space-between;
        gap: 245px;
    }

    &__item span {
        display: block;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }

    &__item p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 690px;
        margin: 0;
    }

    &__images {
        display: grid;
        grid-template-columns: 50% 1fr 1fr 1fr;
        gap: 20px;
        margin-bottom: 75px;
    }

    
    &__pic {
        min-width: 0;
        overflow: hidden;
    }

    &__date {
        display: flex;
        align-items: center;
        gap: 20px;
        flex-wrap: wrap;
        margin-bottom: 50px;
    }

    &__date-text {
        color: #bdb9b8;
        font-size: 14px;
        font-family: 'RobotoOdd', Arial, sans-serif;
        line-height: 1.6;
        font-weight: 400;
        margin: 0;
        white-space: nowrap;
    }

    &__update {
        color: #bdb9b8;
        font-size: 14px;
        font-family: 'RobotoOdd', Arial, sans-serif;
        line-height: 1.6;
        font-weight: 400;
        margin: 0;
        white-space: nowrap;
    }

    &__time {
        font-size: 14px;
        font-family: 'RobotoOdd', Arial, sans-serif;
        line-height: 1.6;
        font-weight: 400;
        margin: 0;
        white-space: nowrap;
    }

    &__jump-text {
        color: #171717;
        font-size: 40px;
        font-family: RobotoCondensed;
        line-height: 1;
        font-weight: 700;
        text-transform: uppercase;
        margin: 0 0 30px;
    }

    &__jump-list {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin: 0 0 100px;
    }

    &__jump-link {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        line-height: 130%;
    }

    &__subtitle {
        color: #171717;
        font-size: 60px;
        font-family: RobotoCondensed;
        line-height: 1;
        font-weight: 700;
        text-transform: uppercase;
        max-width: 70%;
        margin: 0 0 50px;
    }

    &__subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        max-width: 90%;
        margin: 0 0 50px;
    }

    &__img {
        max-width: 100%;
        height: auto; 
        aspect-ratio: 445/600;
        width: 40%;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .accordion {
        width: 80%;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .accordion__item {
        width: 100%;
    }

    .accordion__button {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        align-items: center;
        width: 100%;
    }

    .accordion__button span {
        
        font-family: Inter;
        font-weight: 500;
        font-style: Regular;
        font-size: 25px;
        line-height: 130%;
    }

    .accordion__inner {
        margin-top: 20px;
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        width: 90%;
    }

    .accordion__button svg {
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        transition: .3s;
    }

    .accordion__item.is-active .accordion__button svg {
        transform: rotate(45deg);
    }
}