.portfolio {
    padding: 50px 0;

    .portfolio__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 30px;
    }

    .portfolio__list {
        display: flex; 
        overflow-x: auto; 
        overflow-y: hidden; 
        scroll-behavior: smooth; 
        gap: 16px; 
        padding: 10px 0 25px 0; 
        margin: 0 -20px; 
        padding-left: 20px; 
        -webkit-overflow-scrolling: touch; 
        scrollbar-width: thin; 
        scrollbar-color: black grey;
    }

    .portfolio__item {
        max-width: 300px;
        width: 100%;
        flex: 0 0 auto;
    }

    .portfolio__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 300/350;
        border-radius: 12px;
        overflow: hidden;
    }

    .portfolio__item .small {
        font-family: RobotoCondensed;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
    }

    .portfolio__scroll {
        text-align: center; 
        margin-top: 20px;
        margin-bottom: 40px;
    }

    .portfolio__scroll span {
        font-size: 14px; 
        color: #666; 
        display: inline-block; 
        padding: 5px 10px; 
        background: #f5f5f5; 
        border-radius: 12px;
    }

    .portfolio__link {
        max-width: 220px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        margin: 0 auto;
        transition: .3s;
    }

    .portfolio__link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    .portfolio__link:hover svg {
        transform: rotate(45deg);
    }
}