.event-process {
    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 40px;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
    }

    &__item {
        border: 1px solid grey;
        border-radius: 10px;
        overflow: hidden;
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__item span {

        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 50px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin-bottom: 20px;
    }

    &__name {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 28px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-align: center;
        color: #181818;
        margin: 0 0 20px;
    }

    &__desc {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        line-height: 130%;
        text-align: center;
        margin: 0;
    }
}