.hero-commerce {
    padding: 150px 0 0;

    &__wrapper {
        padding: 50px 0;
        display: flex;
        gap: 20px;
    }

    &__inner {
        width: 50%;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 50px;
    }

    &__image {
        width: 50%;
        max-width: 100%; 
        height: auto; 
        aspect-ratio: 800/800;
        border-radius: 16px; 
        box-shadow: 0 12px 32px rgba(0,0,0,0.2);
        overflow: hidden;
    }

    .hero-copy__list {
        margin-bottom: 24px;
        display: flex;
        flex-direction: column;
        gap: 12px; 
    }

    .hero-copy__item {
        display: flex; 
        align-items: flex-start;
        gap: 10px;
    }

    .hero-copy__svg {
        color: #4ade80; 
        font-size: 18px; 
        flex-shrink: 0;
    }

    .hero-copy__item-text {
        font-size: clamp(15px, 1.6vw, 17px); 
        color: #000;
    }

    &__buttons-top {
        display: flex;
        gap: 10px;
    }

    &__buttons-top-link {
        max-width: 190px;
        width: 100%;
        background: #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #FFFFFF;
        transition: .3s;
    }

    &__buttons-top-link svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__buttons-top-link:hover svg {
        transform: rotate(45deg);
    }

    &__buttons-top-link-white {
        max-width: 190px;
        width: 100%;
        border: 1px solid #181818;
        border-radius: 20px;
        padding: 13px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 12px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: #181818;
        transition: .3s;
    }

    &__buttons-top-link-white svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__buttons-top-link-white:hover svg {
        transform: rotate(45deg);
    }
}