.about {
    padding: 150px 0 0;

    &__wrapper {
        padding: 0 0 50px;
    }

    &__title {
        display: flex;
        flex-direction: column;
    }

    &__title span {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 160px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
    }

    &__title span:last-child {
        margin-left: 180px;
    }

    &__info {
        display: flex;
        margin-left: 180px;
        gap: 17px;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 350/660;
        flex-shrink: 0;
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text-block {
        display: flex;
        flex-direction: column;
        height: 100%;
        flex-grow: 1;
    }

    &__text {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__subtext {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 20px;
    }

    &__bottom {
        margin: 140px 0 0;
        font-family: Inter;
        font-weight: 500;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 200%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
    }
}