.event-pricing {
    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 40px;
    }

    &__box {
        background: #fff;
        padding: 40px;
        border-left: 3px solid #000;
        margin-bottom: 40px;
    }

    &__subtitle {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
    }

    &__subtext {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        line-height: 130%;
        margin: 0;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
        margin: 0 auto;
        justify-items: center;
        width: 90%;
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
    }

    &__item span {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 40px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
    }

    &__item p {
        font-family: Inter;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
    }
}