.faq {
    overflow: hidden;

    &__wrapper {
        padding: 75px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 85px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 40px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    &__item {
        border-bottom: 1px solid #BDB9B8;
        padding-bottom: 40px;
        padding-left: 200px;
    }

    &__name {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 20px;
    }

    &__desc {
        max-width: 650px;
        width: 100%;
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 0 180px;

    }

    &__dop {
        display: flex;
        flex-direction: column;
        gap: 25px;
        margin-top: 20px;
    }

    &__dop-name {
        display: flex;
        gap: 50px;
        
    }

    &__dop-name span {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        display: block;
        margin: 0;
    }

    &__dop-name p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 160%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0;
    }
}