.spots {
    padding: 50px 0;

    .spots__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #000;
        margin: 0 0 40px;
    }

    .spots__top {
        margin-bottom: 30px;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .spots__top p {
        font-family: Inter;
        font-weight: 400;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        
        vertical-align: middle;
        
        margin: 0;
    }

    .spots__list {
        display: grid; 
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); 
        gap: 24px; 
    }

    .spot__card {
        background: #fafafa; 
        border-radius: 12px; 
        overflow: hidden; 
        border: 1px solid #eee;
        display: flex;
        flex-direction: column;
    }

    .spots__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 335/200;
    }

    .spots__info {
        display: flex;
        flex-direction: column;
        padding: 0 15px 20px;
    }

    .spots__info h3 {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        line-height: 110%;
        margin: 0 0 20px;
    }

    .spots__info .small {
        font-family: Inter;
        font-weight: 400;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #666;
        margin: 0;
    }
}