.event-tab {
    &__wrapper {
        padding: 50px 0;
    }

    &__title {
        font-family: RobotoCondensed;
        font-weight: 700;
        font-style: Bold;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        vertical-align: middle;
        text-transform: uppercase;
        color: #181818;
        margin: 0 0 30px;
    }

    &__text {
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: #181818;
        margin: 0 0 40px;
    }

    &__buttons {
        display: flex;
        gap: 20px;
        justify-content: center;
        flex-wrap: wrap;
        margin-bottom: 40px;
    }

    &__btn {
        border: 1px solid #181818;
        color: #181818;
        max-width: 220px;
        width: 100%;
        border-radius: 20px;
        padding: 13px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        font-family: RobotoMono;
        font-weight: 400;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 130%;
        letter-spacing: 0;
        text-align: center;
        vertical-align: middle;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;

        &--active {
            background: #181818;
            color: #fff;
            border: none;
        }
    }

    &__btn:hover {
        transform: scale(1.05);
    }

    &__item {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 40px 20px;
        // transition: .3s;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        z-index: 0;

        &--active {
            opacity: 1;
            visibility: visible;
            position: relative;
            z-index: 1;
            transition: .3s;
        }
    }
}